.topbar {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 6;
  background-color: transparent;
  .nav-menu {
    position: absolute;
    top: 25px;
    right: 25px;
    display: flex;
    flex-flow: row nowrap;
    -ms-flex-flow: row nowrap;
    padding: 5px;
    div {
      margin-left: 20px;
    }
    .avatar {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      cursor: pointer;
      background-color: #000;
      overflow: hidden;
      img {
        width: 100%;
        height: auto;
      }
    }
    .notif {
      margin-top: 3px;
      border: 1px solid transparent;
      border-radius: 50%;
      cursor: pointer;
      svg {
        width: 25px;
        height: 30px;
        fill: #fff;
      }
    }
    .search-bar {
      width: 225px;
      padding: 0 15px;
      background-color: #fff;
      border: 1px solid transparent;
      border-radius: 20px;
      cursor: text;
      #global_search_bar {
        outline: none;
        width: 85%;
        height: 35px;
        border: none;
        background-color: #fff;
        font-size: 12px;
        color: rgb(116, 138, 157);
        &::placeholder {
          font-size: 12px;
          color: rgb(116, 138, 157);
        }
      }
      .icon {
        display: inline-block;
        width: 15%;
        padding-left: 8px;
        svg {
          width: 15px;
          height: 15px;
          fill: rgb(116, 138, 157);
        }
      }
    }
  }
}
