// Colors
$black: #000;
$white: #fff;
$orange: #cd503c;
$light-orange: #dc7431;
$dark-blue-green: #39484c;
$green: #00b050;
$red: #ff3333;
$yellow: #f0cc71;
$dark-orange: #c54d39;
$turquoise: #307f8c;
$gray: #a1a1a4;

// Background
$bg-green: rgba($green, 0.1);
$bg-red: rgba($red, 0.1);
$bg-yellow: rgba($yellow, 0.1);
$bg-light-orange: rgba($light-orange, 0.1);
$bg-dark-orange: rgba($dark-orange, 0.1);
$bg-gray: rgba($gray, 0.1);

// Button
$btn-border-color: #efefef;
$btn-background: #fafafa;
$btn-background-active: $orange;
$btn-border-radius: 4px;

// Font Size
$font-8: 0.5rem;
$font-10: 0.625rem;
$font-12: 0.75rem;
$font-14: 0.875rem;
$font-16: 1rem;
$font-18: 1.125rem;
$font-20: 1.25rem;
$font-22: 1.375rem;
$font-24: 1.5rem;
$font-26: 1.625rem;
$font-28: 1.75rem;
$font-30: 1.875rem;
$font-40: 2.5rem;
$font-50: 3.125rem;

// Font Weight
$font-weight-l: 300;
$font-weight-r: 400;
$font-weight-m: 500;
$font-weight-s: 600;
$font-weight-b: 700;

// Default
$Montserrat: "Montserrat", sans-serif;
$primary-color: #757678;
$secondary-color: #748a9d;
$box-shadow-color: #ff9b76;
$light-text-color: #bfbfbf;
$dark-text-color: #4d4f5c;
$darker-text-color: #43425d;
$border-color: #d8d8d8;

// Table
// start delete
$th-border: $secondary-color;
$tr-border: rgba($secondary-color, 0.2);
$tr-bg-active: rgba($dark-orange, 0.05);
$th-font-size: $font-10;
$td-font-size: $font-14;
// end delete

$tr-hover: #f7f8fb;

// Tabs
$tab-orange: #c5503d;
$tab-orange-active: #df6955;
$tab-turquoise: #35828e;
$tab-turquoise-active: #4d9ba8;

// Filter Options
$separator-color: #ccc;

// Comment Area
$comment-background: #f0f4f8;
$comment-footer-border-color: #eaf0f4;
$mention-color: #307f8c;
