@import "../../layout/variables";
@import "../../layout/mixins";
.modal-open .modal-backdrop {
    background-color: rgba(0, 0, 0, .5);
}

.product-modal {
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    .modal-dialog {
        width: 1000px;
    }
    .modal-content {
        background: transparent;
        border-color: transparent;
    }
    .to-prevent-glitch {
        height: 1rem;
        width: 1rem;
        background: transparent;
        color: transparent;
    }
    .content-wrapper {
        display: flex;
        flex-direction: row;
    }
    .content-container {
        border-radius: 8px;
        border-color: $border-color;
        background: $white;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        min-width: 1000px;
        &.step-1 .btn-container {
            .btn {
                min-height: 164px;
                .icon {
                    margin-bottom: 1rem;
                    height: 60px;
                }
            }
        }
        .responsive-basic-dd {
            position: absolute;
            top: 80px;
            text-align: center;
            transform: translateY(-50%);
            right: 1.8rem;
        }
    }
    .modal-header {
        padding: 1.8rem;
        border-bottom: none;
        height: 130px;
        position: relative;
        .modal-title {
            font-size: $font-16;
            letter-spacing: 2px;
            text-transform: uppercase;
            color: $secondary-color;
            font-weight: $font-weight-s;
            position: absolute;
            top: 50%;
            text-align: center;
            transform: translateY(-50%);
        }
    }
    .modal-body {
        padding: 0 1.8rem;
        display: flex;
        flex-wrap: wrap;
        align-items: start;
        max-height: 550px;
        overflow-y: auto;
        overflow-x: hidden;
        &::-webkit-scrollbar {
            width: 6px;
            background: #ebebeb;
            border-radius: 9px;
        }
        &::-webkit-scrollbar-track {
            border-radius: 9px;
            background: #ebebeb;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 9px;
            background: #a1a1a4;
        }
        .btn-container {
            width: 25%;
            padding: 10px;
            display: inline-block;
            .btn {
                min-height: 164px;
                .icon {
                    margin-bottom: 1rem;
                    height: 60px;
                }
            }
        }
        .btn {
            font-weight: $font-weight-l;
            font-size: $font-12;
            border-color: $border-color;
            width: 100%;
            border-radius: 8px;
            &:hover,
            &.active {
                background-color: $dark-orange;
                color: $white;
                fill: $white;
                .icon {
                    fill: $white !important;
                }
            }
            .icon {
                display: block;
                margin: 0 auto;
                fill: #a1a1a4;
            }
        }
        .search-checkbox {
            width: 100%;
            font-size: $font-14;
            font-weight: $font-weight-s;
            .form-control {
                font-size: $font-12;
                margin-bottom: 1.6rem;
                &:focus {
                    border-color: lighten( $dark-orange, 20%);
                    box-shadow: 0 0 0 0.2rem rgba(58, 176, 195, 0.5);
                }
            }
            .list-of-checkbox {
                width: 98%;
                margin: 0 auto;
                max-height: 430px;
            }
            .custom-checkbox {
                margin-bottom: .6rem;
                &.custom-control {
                    min-height: 16px;
                    padding-left: 30px;
                }
                &.custom-control .custom-control-label::before {
                    top: 2px;
                    left: -30px;
                    width: 16px;
                    height: 16px;
                }
                &.custom-control .custom-control-label::after {
                    top: -2px;
                    left: -33px;
                    width: 23px;
                    height: 23px;
                }
            }
            .custom-control-input:checked~.custom-control-label::before {
                border-color: $dark-orange;
                background-color: $dark-orange;
            }
        }
    }
    .modal-footer {
        padding: 1.4rem 1.8rem;
        border-top: none;
        .btn {
            font-size: $font-10;
            min-width: 66px;
        }
    }
}

@media (min-width: 576px) {
    .modal-dialog {
        max-width: none!important;
    }
}