@import '../../layout/variables';
.executive-cockpit-page {
    .container-metrics-page {
        &.container-fluid {
            padding: 0 50px
        }
        .inner-wrap {
            height: calc( 100vh - 200px);
            min-height: 500px;
            position: relative;
        }
        .block {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
        }
        .content-block {
            padding-right: 5rem;
            .title {
                color: #1E1C24;
                font-size: 3.3rem;
                font-weight: $font-weight-b;
            }
            .text {
                color: #B1AAAA;
                font-size: 1.5rem;
                font-weight: $font-weight-m;
                line-height: 1.8;
                margin-top: 1rem;
                margin-right: 1rem;
            }
            .btn {
                border-radius: 999px;
                margin-top: 1rem;
                font-size: $font-18;
                padding-left: 3rem;
                padding-right: 3rem;
                &.btn-metrics {
                    margin-right: 2rem;
                }
            }
        }
        .image-block {
            .person {
                position: absolute;
                &.person-1 {
                    bottom: -32px;
                    left: -32px;
                }
                &.person-2 {
                    bottom: -32px;
                    right: 23px;
                }
            }
        }
    }
}

.metrics-page {
    display: flex;
    align-items: center;
    .content {
        padding-left: 5rem;
        padding-right: 10rem;
    }
    .title {
        font-size: $font-50;
        color: #1E1C24;
        font-weight: $font-weight-b;
        line-height: normal;
    }
    .text {
        font-size: $font-18;
        color: #B1AAAA;
        font-weight: $font-weight-m;
        padding-top: 1rem;
    }
    .btn-metrics {
        border-radius: 2rem;
        padding: 0.5rem 4rem;
        margin-top: 2rem;
    }
}

.grid-stack {
    background: white;
}

.grid-stack-item-content {
    color: #2c3e50;
    text-align: center;
    background-color: #18bc9c;
}

.metrics-modal {
    &.modal {
        backdrop-filter: blur(6px);
    }
    &.modal-backdrop.show {
        opacity: 0.1;
    }
    &.modal .modal-dialog {
        transform: none;
        width: 280px;
    }
    .modal-header {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        padding: 2rem 2rem 0;
        border-bottom: none;
        border-top-left-radius: calc(0.3rem - 1px);
        border-top-right-radius: calc(0.3rem - 1px);
    }
    .modal-title {
        font-size: $font-12;
        text-transform: uppercase;
        font-weight: $font-weight-s;
        line-height: 2.5;
        letter-spacing: 2px;
        color: #748a9d;
    }
    .narrow-list.filter-modal {
        display: block;
        .title {
            display: none;
        }
    }
    .custom-modal {
        width: 100%;
        z-index: 4;
        padding: 0.75rem 2rem 0;
        background: #fff;
        display: flex;
        flex-direction: column;
        box-shadow: none;
        margin: 0;
    }
    .narrow-list .btn.dropdown-toggle {
        width: 100%;
    }
    .btn-container {
        margin-left: auto;
        margin-right: 0;
        padding: 0 2rem 2rem;
    }
    .btn {
        font-size: 0.625rem;
    }
}

.images {
    position: absolute;
    top: -40%;
    .laptop {
        height: 600px;
    }
    .person-1 {
        position: absolute;
        left: 0;
        top: 50%;
    }
    .person-2 {
        position: absolute;
        top: 65%;
        right: 0.75%;
    }
}

.draggable-div {
    cursor: 'move';
    background: #fff;
    box-shadow: 10px 10px 10px 0px rgba(212, 212, 212, 0.1);
    border: 1px solid #d8d8d8;
    border-radius: 10px;
    height: 100%;
}