        @import '../../layout/variables';
        .home-page {
            div.table {
                display: table;
            }
            
            .tr {
                display: table-row;
            }
            
            .td {
                display: table-cell;
                width: 40px;
            }
            
            .td .indicator,
            .period {
                position: absolute;
            }
            .td.val{
                width: 65px;
            }
            
            .td:nth-child(2) {
                width: 70px;
                font-size: $font-26;
            }
            
            .td:nth-child(3) {
                font-size: $font-12;
            }
            .home-img {
                margin-top: 2rem;
                padding-left: 3rem;
            }
            .title {
                padding: 2rem 3rem 3rem 0;
            }
            .gray {
                background-color: #818E94;
            }
            .page-content {
                padding-left: 30px!important;
            }
            .welcome-container {
                color: #1E1C24;
                display: table;
                height: 100%;
                overflow: hidden;
            }
            .welcome-content {
                display: table-cell;
                vertical-align: middle;
            }
            .header,
            .sub-header {
                padding-left: 2rem;
            }
            .header {
                font-weight: $font-weight-b;
                font-size: $font-30;
            }
            .sub-header {
                padding-top: 1rem;
                font-weight: $font-weight-m;
                font-size: $font-18;
            }
            .card-title {
                margin: 0;
                margin-bottom: 1rem;
                font-size: 0.75rem;
                letter-spacing: 2px;
                text-transform: uppercase;
                font-weight: 600;
                color: #748a9d;
            }
            .btn-gray {
                border: #DBDBDB;
                background-color: #DBDBDB;
                color: #8A8B8D;
                line-height: 0.5rem;
                border-radius: 1rem;
            }
            .table-responsive {
                overflow: hidden;
                .table thead th {
                    border: none;
                    font-weight: 500;
                    font-size: 0.625rem;
                    padding: 0.5rem;
                    outline: none;
                    text-transform: uppercase;
                    color: #748a9d;
                }
            }
            .icon {
                color: #818E94;
                font-size: 1.7rem;
            }
            .height1 td {
                padding: 1rem 0.5rem;
                border-bottom: 1px solid rgba(116, 138, 157, 0.2);
            }
            .div-table {
                .val {
                    width: 70px;
                }
                .custom-width {
                    width: 40px;
                }
                .td:nth-child(1) {
                    font-size: $font-26;
                }
                .td:nth-child(3) {
                    font-size: $font-12;
                }
            }
            .td-content {
                padding: 0.5rem;
                .td-title {
                    font-size: 0.7rem;
                    text-transform: uppercase;
                    font-weight: 600;
                    color: #748A9D;
                }
                .td-text {
                    font-weight: $font-weight-m;
                    color: #737373;
                    font-size: $font-16;
                }
                .td-date {
                    font-size: 0.6rem;
                    text-transform: uppercase;
                    color: #737373;
                }
            }
        }