@import "../../layout/variables";
@import "../../layout/mixins";

.sidebar {
  width: 66px;
  transition: width 0.4s ease-in-out;
  z-index: 5;
  position: fixed;
  @media (orientation: landscape) {
    &.open {
      width: 222px;
    }
  }

  .menu {
    transition: width 0.4s ease-in-out;
    background-color: $dark-blue-green;
    height: 100vh;
    min-height: 460px;
    position: relative;

    @media (orientation: portrait) {
      &.open {
        width: 222px;
      }
    }
    @media (orientation: landscape) {
      &.open {
        width: 222px;
      }
    }

    .header {
      width: 100%;
      overflow: hidden;
      position: relative;
      padding: 1.8rem 1.4rem;
      .logo {
        width: 118px;
        display: block;
        opacity: 1;
        transition: opacity 0.4s ease-in-out;
      }
      .hamburger {
        position: absolute;
        z-index: 10;
        right: 4px;
        top: 51%;
        transform: translateY(-50%);
        width: 66px;
        padding: 20px;
        transition: right 0.4s;
        text-align: center;
        user-select: none;
        cursor: pointer;
        background: $dark-blue-green;
      }
    }
    &.closed {
      width: 66px;
      .header .hamburger {
        right: 0;
      }
      .header .logo {
        opacity: 0;
      }
    }
    .content {
      margin-top: 0.5rem;
    }
    ul {
      @include reset-list;

      &.bottom {
        position: absolute;
        bottom: 0;
        width: 100%;
      }

      .item {
        a {
          color: $white;
          display: block;
          padding: 0.9rem 0;
          margin-bottom: 1px;
          &.active,
          &.active:hover {
            background-color: $orange;
          }
          &:hover {
            background-color: rgba($black, 0.1);
          }
        }
        .icon {
          height: 17px;
          width: 66px;
          fill: $white;
        }
        span {
          display: inline-block;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          vertical-align: middle;
          width: calc(100% - 70px);
          position: relative;
          left: -10px;
        }
      }
    }
  }
}
