@import "../../layout/variables";
@import "../../layout/mixins";

.wide-list {
  @include horizontal-list;

  li {
    padding: 0.5rem 1rem;
    margin-right: 0.5rem;
    cursor: pointer;
    border-radius: $btn-border-radius;
    background-color: $btn-background;
    font-size: $font-12;
    color: $primary-color;
    border: 1px solid $btn-border-color;
    font-size: $font-10;
    font-weight: $font-weight-s;

    &:last-child {
      margin-right: 0;
    }

    &.active {
      background-color: $orange;
      color: $white;
    }
  }
}

.narrow-list {
  display: inline-block;

  &.filter-modal {
    display: none;
  }

  .btn.dropdown-toggle {
    font-size: $font-10;
    font-weight: $font-weight-s;
    min-width: 148px;
    text-align: left;
    vertical-align: middle;
    position: relative;
    padding-right: 2.2rem;

    &.hasPick{
      background-color: $orange;
      color: $white;
      
      svg{
        fill: $white;
      }
    }
  }

  svg {
    width: 10px;
    position: absolute;
    top: 50%;
    right: 1rem;
    transform: translateY(-50%);
  }

  .dropdown-toggle::after {
    content: none;
  }

  .dropdown-menu.show {
    padding: 0.9rem;
  }

  .custom-checkbox,
  .custom-radio {
    margin-bottom: 0.5rem;
  }

  .btn-block {
    text-align: right;
    margin-top: 1rem;
    .btn {
      margin: 3px;
      padding: 0.3rem 0.6rem;
    }
  }
  .btn {
    position: relative;
  }

  .list-of-checkbox {
    padding-left: 0;
    list-style: none;
    max-height: 200px;
    overflow-y: auto;
    overflow-x: hidden;
  }

  .form-control {
    font-size: $font-10;
    border-radius: 0.1rem;
  }
}

.basic-dd {
  .btn.dropdown-toggle {
    font-size: $font-10;
    font-weight: $font-weight-s;
    min-width: 148px;
    text-align: left;
    vertical-align: middle;
    position: relative;
    padding-right: 2.2rem;
  }

  svg {
    width: 10px;
    position: absolute;
    top: 50%;
    right: 1rem;
    transform: translateY(-50%);
  }
}

.basic-dd,
.responsive-basic-dd {
  .dropdown-menu.show {
    padding: 0.5rem 0;
  }
}

.responsive-basic-dd,
.responsive-radio-dd {
  display: inline-block;

  &.hidden {
    display: none !important;
  }

  &.visible {
    display: inline-block !important;
  }
}

.show-top-bottom-dd{
  .custom-radio{
    display: inline-block;

    &:first-child{
      margin-right: 1rem;
    }
  }
  .form-control+.custom-radio{
    margin-right: 1rem;
  }
  .form-control{
    margin: .25rem 0 .8rem;
    border-radius: 0.1rem;
  }
  .dropdown-menu{
    min-width: 190px;
  }
}

.separator {
  display: inline-block;
  margin: 0 1rem;
  vertical-align: middle;
  width: 0;
  overflow: hidden;
  border-right: 1px solid rgba(204, 204, 204, 0.5);
}
