@import "../../layout/variables";
@import "../../layout/mixins";

.comment-area {
  width: 0;
  transition: width 0.4s ease-in-out;
  position: fixed;
  right: 0;
  top: 0;
  padding-top: 154px;
  background: #f0f4f8;
  z-index: 3;
  @media (orientation: landscape) {
    &.open {
      width: 408px;
    }
  }

  .comment-content {
    right: 0;
    width: 408px;
    padding: 1.2rem 2rem;
    transition: right 0.4s ease-in-out;
    height: 100%;
    min-height: calc(100vh - 154px);
    background: $comment-background;
    position: relative;
    overflow: hidden;

    @media (orientation: portrait) {
      &.open {
        right: 408px;
      }
    }
    @media (orientation: landscape) {
      &.open {
        right: 0;
      }
    }
  }

  .close {
    position: relative;
    outline: none;
  }

  .title {
    text-transform: uppercase;
    font-weight: $font-weight-s;
    font-size: $font-14;
    color: $secondary-color;
    margin: 0 0 1.2rem 0.8rem;
  }

  .owner {
    display: table;
    width: calc(100% - 0.8rem);
    margin: 0 0 1.2rem 0.8rem;

    .owner-dp,
    .owner-details {
      display: table-cell;
      vertical-align: middle;
    }

    .owner-dp {
      width: 48px;
      height: 33px;
      border-radius: 999px;
      overflow: hidden;
      word-break: break-all;
      padding-right: 1rem;
      img {
        width: 33px;
        height: 33px;
        border-radius: 999px;
        overflow: hidden;
        word-break: break-all;
      }
    }

    .owner-details {
      p {
        margin: 0;
      }
      .name {
        font-size: $font-14;
        font-weight: $font-weight-s;
      }
      .email {
        display: inline-block;
        margin-left: 0.4rem;
        font-size: $font-10;
        color: $secondary-color;
      }
      .position {
        color: $light-text-color;
        font-size: $font-10;
      }
    }
  }

  .comment-list {
    padding-right: 0.5rem;
    max-height: calc(100vh - 420px);
    overflow-y: auto;
  }

  .comment-block {
    background-color: $white;
    margin-bottom: 1rem;

    &:last-child {
      margin-bottom: 0;
    }

    .comment-details {
      padding: 1.8rem 2rem 0.6rem;
      font-size: $font-10;
      font-weight: $font-weight-r;
    }

    .user {
      display: table;
      width: 100%;
      margin-bottom: 0.8rem;

      .user-dp,
      .user-details {
        display: table-cell;
        vertical-align: middle;
      }

      .user-dp {
        width: 48px;
        height: 33px;
        border-radius: 999px;
        overflow: hidden;
        word-break: break-all;
        padding-right: 1rem;
        img {
          width: 33px;
          height: 33px;
          border-radius: 999px;
          overflow: hidden;
          word-break: break-all;
        }
      }

      .user-details {
        p {
          margin: 0;
        }
        .name {
          font-size: $font-14;
          font-weight: $font-weight-s;
        }
        .position {
          color: $light-text-color;
          display: flex;
          align-items: center;
          .dateTime {
            margin-left: auto;
            text-align: right;
            min-width: 60px;
          }
        }
      }
    }

    .message {
      font-size: $font-12;
      line-height: 1.8;
      padding-left: 3rem;
      margin-bottom: 1rem;

      .tag {
        color: $mention-color;
        cursor: pointer;
        &:hover {
          text-decoration: underline;
        }
      }
    }

    .interactions {
      color: $light-text-color;
      span {
        display: inline-block;

        &:first-child {
          margin-right: 1rem;
        }
      }
    }

    .commnet-footer {
      border-top: 1px solid $comment-footer-border-color;
      font-size: $font-10;
      .btn-group {
        width: 100%;
        .btn {
          border-radius: 0;
          padding: 0.84rem 0;
          text-align: center;
          border-color: transparent;
          vertical-align: middle;
          box-shadow: none;
        }

        .icon {
          vertical-align: middle;
          width: 13px;
          margin-right: 0.2rem;
        }
      }
    }
  }

  .add-comment {
    padding-top: 1rem;
    position: absolute;
    width: calc(100% - 4rem);
    background: $comment-background;
    bottom: 1rem;
    left: 2rem;

    &:before {
      position: absolute;
      top: 0;
      left: -2rem;
      content: "";
      border-top: 1px solid rgba($primary-color, 0.5);
      display: block;
      width: calc(100% + 4rem);
    }

    textarea.form-control {
      font-size: $font-12;
      border-color: transparent;
      margin-bottom: 0.6rem;
      resize: none;
      font-weight: $font-weight-r;
    }

    .option {
      display: flex;
      align-items: center;

      .custom-checkbox {
        font-size: $font-10;
        font-weight: $font-weight-s;
      }

      .btn {
        margin-left: auto;
        font-size: $font-10;
      }
    }
  }
}

.mention-block {
  margin-bottom: 0.8rem;
  background: $white;
}

.mention-block--singleLine .mention-block__control {
  display: inline-block;
  width: 130px;
}

.mention-block--singleLine .mention-block__highlighter {
  padding: 1px;
  border: 2px inset transparent;
}
.mention-block--singleLine .mention-block__input {
  padding: 1px;
  border: 2px inset;
  color: $primary-color;
}

.mention-block--multiLine .mention-block__control {
  font-family: $Montserrat;
  line-height: 1.8;
  font-size: $font-12;
  font-weight: $font-weight-r;
}

.mention-block--multiLine .mention-block__highlighter {
  border: 1px solid transparent;
  padding: 1.2rem 2rem;
  max-height: 128px;
  overflow: auto;
}

.mention-block--multiLine .mention-block__input {
  border: 1px solid transparent;
  padding: 1.2rem 2rem;
  outline: 0;
  color: $primary-color;
  min-height: 20px;
  max-height: 128px;
  overflow: auto !important;
}

.mention-block__suggestions {
  z-index: 3 !important;
}

.mention-block__suggestions__list {
  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.15);
  font-size: 10pt;
  position: relative;
}

.mention-block__suggestions__item {
  padding: 5px 15px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}

.mention-block__suggestions__item--focused {
  background-color: #cee4e5;
}

.mention {
  position: relative;
  z-index: 1;
  color: $mention-color;
  text-shadow: 1px 1px 1px white, 
    1px -1px 1px white, 
    -1px 1px 1px white,
    -1px -1px 1px white;
  pointer-events: none;
}

.mention-block__input::-webkit-scrollbar {
  width: 6px;
  background: #ebebeb;
  border-radius: 9px;
}

.mention-block__input::-webkit-scrollbar-track {
  border-radius: 9px;
  background: #ebebeb;
}

.mention-block__input::-webkit-scrollbar-thumb {
  border-radius: 9px;
  background: #a1a1a4;
}
