.amcharts-ZoomOutButton-group .amcharts-RoundedRectangle-group {
    fill: #A1A1A4;
}

.stacked-column-bar {
    margin: 8px 0 0 0;
    display: flex;
    flex-direction: row;
    height: 1em;
    overflow: hidden;
    width: 100%;
    .stacked-bar {
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.stacked-column-chart {
    position: relative;
    width: 100%;
    height: 100%;
    .amCharts-Container {
        .amcharts-XYChart-group {
            color: black;
        }
    }
    .amcharts-myValueAxis .amcharts-AxisLabel-group {
        fill: #A6BCD0;
        font-size: 12px;
    }
    .amcharts-myValueAxis .amcharts-Grid {
        display: none;
    }
    .amcharts-myCategoryAxis .amcharts-AxisLabel-group {
        fill: #748A9D;
        font-size: 12px;
    }
    .amcharts-Label-group,
    .amcharts-LabelBullet-group {
        // fill: #748A9D;
        font-size: 13px;
    }
}

.stacked-legend {
    height: 50px;
    margin-left: 30px;
    .amcharts-Container-group {
        margin: 0;
        border: 1px solid #000;
    }
    .amcharts-Label-group {
        fill: #748A9D;
        font-weight: 400;
        font-size: 8px;
    }
}

.line-chart {
    width: 100%;
    height: 100%;
    // position: relative;
    .amcharts-myValueAxis .amcharts-AxisLabel-group {
        fill: #A6BCD0;
        font-size: 12px;
    }
    .amcharts-Grid-group {
        fill: #748A9D;
    }
    .amcharts-myCategoryAxis .amcharts-AxisLabel-group {
        fill: #748A9D;
        font-size: 12px;
    }
    .amcharts-Label-group,
    .amcharts-LabelBullet-group {
        fill: #748A9D;
        font-size: 13px;
    }
}

.bubble-chart {
    width: 100%;
    height: 100%;
    .amcharts-myValueAxis .amcharts-AxisLabel-group {
        fill: #A6BCD0;
        font-size: 12px;
    }
    .amcharts-Grid-group {
        fill: #748A9D;
    }
    .amcharts-myCategoryAxis .amcharts-AxisLabel-group {
        fill: #A6BCD0;
        font-size: 12px;
    }
    .amcharts-Label-group,
    .amcharts-LabelBullet-group {
        fill: #748A9D;
        font-size: 13px;
    }
    .amcharts-LabelBullet .amcharts-Label-group {
