@import "../../layout/variables";
@import "../../layout/mixins";

.login-page {
  overflow-x: hidden;
  background: $white url(../../assets/images/background-pattern-02.svg) no-repeat top left;
  background-position-y: 1020px;

  .title{
    font-size: $font-50;
    font-weight: $font-weight-b;
    color: #1E1C24;
  }

  .section-1 {
    min-height: 1020px;
    align-items: center;
    background: url(../../assets/images/background-colorful.svg) no-repeat top right;

    .login-block {
      background: #fff;
      border-radius: 8px;
      border: 1px solid rgba($border-color, 0.8);
      padding: 6rem 4rem;
      box-shadow: 0 5px 20px rgba(#999, 0.15);
      max-width: 653px;
      margin: 0 auto;
      margin-bottom: 5rem;
  
      .logo {
        margin-bottom: 2rem;
        svg{
          width: 250px;
        }
      }
  
      p {
        font-size: $font-18;
        margin-bottom: 3rem;
      }
  
      .form-group {
        margin-bottom: 4rem;
      }
  
      .form-control {
        border-color: transparent;
        border-bottom-color: $btn-border-color;
        color: $darker-text-color;
  
        &:focus {
          box-shadow: none;
          border-bottom-color: darken($btn-border-color, 10%);
        }
      }
  
      .btn-block {
        display: table;
        width: 100%;
        margin-bottom: 5rem;
  
        .btn {
          display: table-cell;
          width: 45%;
          border-radius: 999px;
          font-size: $font-18;
  
          &:first-child {
            margin-right: 10%;
          }
        }
      }
  
      .terms {
        display: block;
        color: $darker-text-color;
        font-size: $font-12;
        text-decoration: none;
      }
    }
  
    .image-block{
      position: relative;
      text-align: center;
      padding: 3rem 5rem;
      top: 10px;

      .person{
        position: absolute;
        left: 0;
        bottom: 50px;
  
        &.person-1{
          left: -80px;
        }
        &.person-2{
          left: 5px;
        }
      }
    }
  }

  .section-2{
    text-align: center;
    padding: 15rem 0;

    .title{
      margin-bottom: 4rem;
    }

    .featured-list{
      @include horizontal-list;

      li{
        margin-right: 2rem;
        margin-bottom: 2rem;

        &:last-child{
          margin-right: 0;
        }
      }
      a{
        display: block;
        text-transform: uppercase;
        color: #39484C;
        border: 1px solid $border-color;
        border-radius: 8px;
        padding: 4rem 2rem;
        font-size: $font-18;
        min-width: 289px;
        background-color: #fff;
        transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

        &:hover{
          text-decoration: none;
          background-color: #e2e6ea;
          border-color: #dae0e5;
        }

        .icon{
          display: block;
          width: 100px;
          height: 100px;
          margin: 0 auto;
          fill: #39484C;
          margin-bottom: 2rem;
        }
      }
    }
  }

  .wrapper{
    max-width: 80%;
    margin: 0 auto;
    .row{
      align-items: center;
      .desc{
        font-size: $font-18;
        color: #B1AAAA;
        line-height: 1.8;
        font-weight: $font-weight-m;
      }
      .image-block{
        position: relative;
        text-align: center;
  
        .person{
          position: absolute;
        }
      }
      .btn-learn{
        border-radius: 999px;
        font-size: $font-22;
        padding: .6rem 3rem;
      }
    }
  }
  
  .section-3,
  .section-4,
  .section-5{
    padding-bottom: 15rem;
    align-items: center;
    .image-block{
      padding-right: 2rem;
    }
    .title{
      margin-bottom: 1rem;
    }
    .desc{
      margin-bottom: 5rem;
    }
    .person{
      top: 50%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%);
    }
  }
  .section-3{
    .person-1{
      top: 75%;
      left: 55%;
    }
  }
  .section-4{
    .image-block{
      padding-right: 0;
      padding-left: 2rem;
    }
    .person-1{
      top: 75%;
      left: 2%;
    }
  }
  .section-5{
    .person-1{
      top: 81%;
      left: 7%;
    }
    .person-2{
      top: 80%;
      left: 83%;
    }
  }
}

.btn.btn-request {
  color: $dark-text-color;
}

.btn.btn-scroll {
  border-radius: 999px;
  font-size: $font-14;
  font-weight: $font-weight-r;
  padding-top: .6rem;

  .icon {
    position: relative;
    top: -2px;
    width: 2rem;
    height: 1.2rem;
    fill: lighten($primary-color, 10%);
  }

  &:focus {
    box-shadow: none;
  }
}
